import React, { useEffect, useState, useContext } from "react";
import "../registerProduct.css";
import { Link } from "react-router-dom";
import { useHistory } from "react-router";
import { useLocation } from "react-router-dom";
import ProductContext from "../contexts/products/ProductContext";

const RegisterProduct = (props) => {
  const context = useContext(ProductContext);
  const { productPicturePath } = context;

  const location = useLocation();
  const updateProductId = location.search.slice(
    location.search.indexOf("=") + 1
  );

  const [imgCount, setImgCount] = useState(0);
  // console.log(updateProductId);

  const { host } = props;
  const history = useHistory();
  let authToken;
  const [sellerUser, setSellerUser] = useState(null);

  // state for product
  const [product, setProduct] = useState({
    name: "",
    description: "",
    sellingPrice: "",
    actualPrice: "",
    category: "Select category",
    course: "Select course",
    tags: "",
    quantity: "",
    productImages: [],
    availability: "available"
  });

  const updateEvents = () => {
    // Removing the image from selected images
    const removeImg = document.getElementsByClassName("removeImg");
    // console.log(removeImg.length);
    Array.from(removeImg).forEach((ele) => {
      ele.addEventListener("click", (e) => {
        ele.parentElement.style.display = "none";
        let toRem = ele.parentNode.children[0];
        toRem.classList.replace("previewImage", "removed");
        let temp = localStorage.getItem("fetnImages");
        temp = JSON.parse(temp);
        let i = temp.indexOf(toRem.src);
        // console.log(i);
        temp.splice(i, 1);
        localStorage.setItem("fetnImages", JSON.stringify(temp));
        if (temp.length < 1) {
          const preview = document.getElementById("preview");
          preview.innerHTML = `
            Preview uploaded images
          `;
        }
      });
    });
  };

  useEffect(() => {
    // Fetching authToken from the localStorage
    // eslint-disable-next-line
    authToken = localStorage.getItem("auth-token");
    if (!authToken) {
      history.push("/login");
    }
    if (!sellerUser) {
      // console.trace()
      // elements where user details will be filled
      const sellerName = document.getElementById("sellerName");
      const sellerEmail = document.getElementById("sellerEmail");
      const contact = document.getElementById("contact");
      const whatsapp = document.getElementById("whatsapp");

      // API request for user's self information
      fetch(`${host}/auth/getSelfInfo/`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "auth-token": authToken
        }
      })
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          // console.log(res);
          if (!res.success || res.error) {
            history.push("/login");
          }
          setSellerUser(res.user);
          sellerName.innerText = res.user.name;
          sellerEmail.innerHTML = `<i style='font-size: "18px", margin-right: "4px"' class="fa fa-envelope"></i> ${res.user.email}`;
          sellerEmail.href = `mailto:${res.user.email}`;
          contact.innerHTML = `<i style='font-size: "18px", margin-right: "4px"' class="fa fa-phone"></i> ${res.user.contact}`;
          contact.href = `tel:${res.user.contact}`;
          whatsapp.innerHTML = `<i style='font-size: "18px", margin-right: "4px"' class="fa fa-whatsapp"></i> ${res.user.contact}`;
          whatsapp.href = `https://wa.me/91${res.user.contact}`;
        });
    }

    if (updateProductId && product.name.length < 1) {
      // console.log(6);
      fetch(`${host}/products/getproduct/${updateProductId}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          const preview = document.getElementById("preview");
          // console.log(result);
          if (result.success) {
            setProduct(result.product);
            
            preview.innerHTML = "";
            localStorage.setItem("fetnImages", JSON.stringify([]));
            result.product.productImages.forEach((src) => {
              fetch(productPicturePath + src)
                .then(function (response) {
                  return response.blob();
                })
                .then(function (blob) {
                  let reader = new FileReader();
                  //   console.log(blob);
                  reader.readAsDataURL(blob);
                  reader.addEventListener("load", () => {
                    let imagesCollection =
                      document.getElementsByClassName("previewImage");
                    //   console.log(Array.from(imagesCollection).length);
                    if (
                      Array.from(imagesCollection).length <
                      result.product.productImages.length
                    ) {
                      let blobSrc = reader.result;
                      let temp = localStorage.getItem("fetnImages");
                      temp = JSON.parse(temp);
                      temp.push(blobSrc);
                      localStorage.setItem("fetnImages", JSON.stringify(temp));
                      // console.log(blobSrc);
                      temp = temp.push(blobSrc);
                      preview.innerHTML += `
                                <div class="imgPreviewDiv">
                                    <img class="previewImage" src="${blobSrc}" alt="previewImage" />
                                <div class="removeImg"><i class="fa fa-close"></i></div>
                                </div>
                            `;
                      updateEvents();
                    }
                  });
                });
            });
          }
        });
    }
  }, []);
  var i = 0;

  const handleInputFiles = () => {
    const preview = document.getElementById("preview");
    let imagesCollection = document.getElementsByClassName("previewImage");
    imagesCollection = Array.from(imagesCollection);
    if (imagesCollection.length === 0) {
      localStorage.removeItem("fetnImages");
      preview.innerHTML = "";
    }
    let sizeFaults = 0;
    let pngFaults = 0;
    const fileTag = document.getElementById("files");
    let src;
    if (preview.innerHTML === "Preview uploaded images") {
      preview.innerHTML = "";
    }
    for (i = 0; i < Math.min(3, fileTag.files.length); i++) {
      // console.log(fileTag.files[i]);
      if (fileTag.files[i].size / 1024 > 1536) {
        sizeFaults++;
        continue;
      }
      // console.log(/.png/.test(fileTag.files[i].name))
      if (!/.png/.test(fileTag.files[i].name)) {
        pngFaults++;
        continue;
      }
      if (fileTag.files[i]) {
        let reader = new FileReader();
        reader.readAsDataURL(fileTag.files[i]);
        // console.log(fileTag.files[i]);
        // eslint-disable-next-line
        reader.addEventListener("load", () => {
          src = reader.result;
          let imagesCollection =
            document.getElementsByClassName("previewImage");
          imagesCollection = Array.from(imagesCollection);
          // console.log(imagesCollection);
          if (imagesCollection.length < 3) {
            let localStoredArr = localStorage.getItem("fetnImages");
            if (!localStoredArr) {
              localStoredArr = [];
            } else {
              localStoredArr = JSON.parse(localStoredArr);
            }
            localStoredArr.push(src);
            localStorage.setItem("fetnImages", JSON.stringify(localStoredArr));

            preview.innerHTML += `
                                <div class="imgPreviewDiv">
                                <img class="previewImage" src="${src}" alt="previewImage${
              i + 1
            }" />
                                <div class="removeImg"><i class="fa fa-close"></i></div>
                                </div>
                            `;
            updateEvents();
          } else {
            preview.innerHTML = "";
            // localStorage.removeItem("fetnImages");
            let newArr = [];
            for (let index = 1; index < 3; index++) {
              // console.log(imagesCollection[i]);
              newArr.push(imagesCollection[index].src);
              preview.innerHTML += `
                                <div class="imgPreviewDiv">
                                <img class="previewImage" src="${imagesCollection[index].src}" height="100px" alt="previewImage${i}" />
                                <div class="removeImg"><i class="fa fa-close"></i></div>
                                </div>
                            `;
            }
            newArr.push(src);
            localStorage.setItem("fetnImages", JSON.stringify(newArr));
            preview.innerHTML += `
                                <div class="imgPreviewDiv">
                                <img class="previewImage" src="${src}" height="100px" alt="previewImage${i}" />
                                <div class="removeImg"><i class="fa fa-close"></i></div>
                                </div>
                            `;
            updateEvents();
            // console.log(77);
          }
          // console.log(localStorage.getItem("fetnImages"));
        });
      }
    }
    if (sizeFaults) {
      alert(
        `${sizeFaults} image(s) are exceeding size limit. Try again after compressing it!`
      );
    }
    if (pngFaults) {
      alert(
        `${pngFaults} image(s) are not in png format. Try again after converting it!`
      );
    }
  };

  const submitForm = (e) => {
    e.preventDefault();
    authToken = localStorage.getItem("auth-token");
    let imagesCollection = document.getElementsByClassName("previewImage");
    imagesCollection = Array.from(imagesCollection);
    if (imagesCollection.length < 1) {
      return alert("Upload atleast one image of the product!");
    }
    // console.log("Submitted");
    // Submit through api
    if (product.category === "Select category") {
      setProduct({ ...product, category: null });
    }
    if (product.course === "Select course") {
      setProduct({ ...product, course: null });
    }
    let tags = product.tags;
    // console.log(product);
    if(typeof(product.tags) === "string"){
        tags = tags.split(",");
    }
    // console.log(tags);
    for (let index = 0; index < tags.length; index++) {
      tags[index] = tags[index].trim();
      tags[index] = tags[index].toLowerCase()
    }


    if(updateProductId){
      // API request to update product
      fetch(`${host}/products/editproduct/${updateProductId}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          "auth-token": authToken
        },
        body: JSON.stringify({
          ...product,
          tags,
          productImages: JSON.parse(localStorage.getItem("fetnImages"))
        })
      })
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          if (res.success) {
            props.showAlert("Product details updated successfully!");
            history.push(`/productDetail?product=${res.updatedProduct._id}`);
          } else {
            // console.log(res);
            if (res.message.msg) {
              props.showAlert("Warning : " + res.message.msg);
            } else {
              props.showAlert("Warning : " + res.message);
            }
          }
        });
    }
    else {
      // API request to create product
      fetch(`${host}/products/listproduct`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": authToken
        },
        body: JSON.stringify({
          ...product,
          tags,
          productImages: JSON.parse(localStorage.getItem("fetnImages"))
        })
      })
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          if (res.success) {
            props.showAlert("Product listed successfully!");
            history.push(`/productDetail?product=${res.product._id}`);
          } else {
            // console.log(res);
            if (res.message.msg) {
              props.showAlert("Warning : " + res.message.msg);
            } else {
              props.showAlert("Warning : " + res.message);
            }
          }
        });
    }
  };

  const changeProductDetails = (e) => {
    setProduct({ ...product, [e.target.name]: e.target.value });
  };

  return (
    <div className="container">
      <form onSubmit={submitForm} className="gridForm">
        <div className="productsDetails">
          <div className="headingsGroup">Product Details</div>
          <div className="detailsGrid">
            <label className="labelGroup" htmlFor="name">
              Selling product :{" "}
            </label>
            <input
              onChange={changeProductDetails}
              value={product.name}
              minLength={1}
              placeholder="Shivani Operating System"
              className="inputGroup"
              name="name"
              id="name"
              type="text"
              required
            />

            <label className="labelGroup" htmlFor="about">
              About product :{" "}
            </label>
            <textarea
              style={{ resize: "none" }}
              onChange={changeProductDetails}
              value={product.description}
              minLength={10}
              className="inputGroup"
              name="description"
              id="about"
              cols="25"
              rows="8"
              required
            ></textarea>

            <label className="labelGroup" htmlFor="sellingPrice">
              Selling price (in rupees) :{" "}
            </label>
            <input
              onChange={changeProductDetails}
              value={product.sellingPrice}
              placeholder="60"
              className="inputGroup"
              name="sellingPrice"
              id="sellingPrice"
              type="number"
              required
            />

            <label className="labelGroup" htmlFor="name">
              MRP (in rupees) :{" "}
            </label>
            <input
              onChange={changeProductDetails}
              value={product.actualPrice}
              placeholder="110"
              className="inputGroup"
              name="actualPrice"
              id="actualPrice"
              type="number"
              required
            />

            <label className="labelGroup" htmlFor="availability">
              Availability status{" "}
            </label>
            <select
              onChange={changeProductDetails}
              value={product.availability ? product.availability : "available"}
              className="inputGroup"
              name="availability"
              id="availability"
              required
            >
              <option value="available">available</option>
              <option value="not available">not available</option>
              <option value="sold">sold</option>
            </select>

            <label className="labelGroup" htmlFor="category">
              Category{" "}
            </label>
            <select
              onChange={changeProductDetails}
              value={product.category ? product.category : ""}
              className="inputGroup"
              name="category"
              id="category"
              required
            >
              <option value="Select category">Select category</option>
              <option value="Textbook">Textbook</option>
              <option value="Guide">Guide</option>
              <option value="Novel">Novel</option>
              <option value="Biography">Biography</option>
              <option value="Tools">Tools</option>
              <option value="Others">Others</option>
            </select>

            <div className="labelGroup">
              <label htmlFor="course">course </label>
              <br />
              <small style={{ fontSize: "12px" }}>
                (If related to any course)
              </small>
            </div>
            <select
              onChange={changeProductDetails}
              value={product.course ? product.course : ""}
              className="inputGroup"
              name="course"
              id="course"
            >
              <option value="Select course">Select course</option>
              <option value="B.Tech">B.Tech</option>
              <option value="B.Sc">B.Sc</option>
              <option value="B.A">B.A</option>
              <option value="M.Tech">M.Tech</option>
              <option value="M.Sc">M.Sc</option>
              <option value="M.A">M.A</option>
              <option value="MBBS">MBBS</option>
              <option value="PhD">PhD</option>
              <option value="JEE">JEE</option>
              <option value="NEET">NEET</option>
              <option value="10th grade">10th grade</option>
              <option value="12th grade">12th grade</option>
            </select>

            <div className="labelGroup">
              <label htmlFor="tags">Tags </label>
              <br />
              <small style={{ fontSize: "12px" }}>
                (Tags are keywords your items will be indexed for, seprate tags
                by commas)
              </small>
            </div>
            <textarea
              onChange={changeProductDetails}
              value={product.tags}
              placeholder="shivani,os,operating system,second year,fourth semester"
              className="inputGroup"
              name="tags"
              id="tags"
              cols="25"
              rows="4"
              required
            ></textarea>

            <label className="labelGroup" htmlFor="quantity">
              Quantity :{" "}
            </label>
            <input
              onChange={changeProductDetails}
              value={product.quantity}
              placeholder="1"
              className="inputGroup"
              name="quantity"
              id="quantity"
              type="number"
              required
            />
          </div>
        </div>

        <div className="productImagesAndSeller">
          <div className="headingsGroup">Product View</div>
          <div className="producyImagesContainer">
            <label className="labelGroup" htmlFor="files">
              Uplaod product images :{" "}
            </label>
            <input
              onChange={handleInputFiles}
              type="file"
              name="files"
              id="files"
              multiple
            />{" "}
            <br />
            <small style={{ color: "red", fontWeight: "bold" }}>
              Note : Upload atmost 3 PNG files and size of each file must not
              exceed 1.5mb
            </small>
            <div id="preview">Preview uploaded images</div>
          </div>

          <div className="headingsGroup">About Seller</div>
          <div className="SellerGrid">
            <label className="sellerLabel" htmlFor="name">
              Name :{" "}
            </label>
            <p id="sellerName" className="sellerDetails">
              Abhijeet Nema
            </p>

            <label className="sellerLabel" htmlFor="email">
              Email :{" "}
            </label>
            <a
              rel="noreferrer"
              target="_blank"
              id="sellerEmail"
              className="sellerDetails"
              href="mailto:abhinema3@gmail.com"
            >
              {" "}
              <i
                style={{ fontSize: "18px", marginRight: "4px" }}
                className="fa fa-envelope"
              ></i>{" "}
              abhinema3@gmail.com
            </a>

            <label className="sellerLabel" htmlFor="contact">
              Contact Number :{" "}
            </label>
            <a
              rel="noreferrer"
              target="_blank"
              id="contact"
              className="sellerDetails"
              href="tel:7389056709"
            >
              <i
                style={{ fontSize: "18px", marginRight: "4px" }}
                className="fa fa-phone"
              ></i>{" "}
              7389056709
            </a>

            <label className="sellerLabel" htmlFor="whatsapp">
              Contact on WhatsApp :{" "}
            </label>
            <a
              rel="noreferrer"
              target="_blank"
              id="whatsapp"
              className="sellerDetails"
              href="https://wa.me/917389056709"
            >
              <i
                style={{ fontSize: "18px", marginRight: "4px" }}
                className="fa fa-whatsapp"
              ></i>{" "}
              7389056709
            </a>
          </div>

          <div className="submitContainer">
            {/* Add terms and condtions */}
            <span>
              I accept the{" "}
              <Link to="terms_and_conditions">terms and conditions</Link>. All
              the information provided by me are checked and true.{" "}
            </span>{" "}
            <input
              type="checkbox"
              name="acceptanceCheckbox"
              id="acceptanceCheckbox"
              required
            />
            <div className="btnDiv">
              {!updateProductId ? (
                <button className="submitBtn" type="submit">
                  Register product
                </button>
              ) : (
                <button className="submitBtn" type="submit">
                  Update details
                </button>
              )}
            </div>
          </div>
        </div>
      </form>
    </div>
  );
};

export default RegisterProduct;
