import ProductContext from "./ProductContext";
import { useState } from "react";

const ProductState = (props) => {
    // api host
    const host = "http://20.212.1.244";
    
    // Sorting method for serach results
    // const [sorting, setSorting] = useState(null);
    // Paths for images
    const userPicturePath = host + "/Images/users/";
    const productPicturePath = host + "/Images/products/";
    // States
    const [user, setUser] = useState(null)
    // const [detailProductId, setDetailProductId] = useState(null);
    // const [searchQuery, setSearchQuery] = useState(null)
    // Alert controlling
    const [alertMessage, setAlertMessage] = useState(null);
    
    const dismissAlert = () => {
        setAlertMessage(null);
    }

    const showAlert = (msg) => {
        setAlertMessage(msg);
        setTimeout(() => {
            dismissAlert();
        }, 4000);
    }

    // API related functions
    const fetchUser = () => {
        const authToken = localStorage.getItem("auth-token")
        if (authToken) {
            // console.log(authToken);
            // fetching user through api
            // API request for user's self information
            fetch(`${host}/auth/getSelfInfo/`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    "auth-token": authToken
                }
            }).then((res) => {
                return res.json();
            }).then((result) => {
                // console.log(result);
                if (result.success) {
                    setUser(result.user);
                    // console.log(user);
                }
            })
        }
    }

    const notifyUser = (message, notifiyingUser) => {
        // console.log(notifiyingUser);
      fetch(`${host}/auth/addNotification`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("auth-token")
        },
        body: JSON.stringify({
          id: notifiyingUser._id,
          notification: message
        })
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          if (result.success) {
            return true
          }
        });
    };

    return (
        <ProductContext.Provider value={{ alertMessage, showAlert, dismissAlert, host, user, setUser, fetchUser,userPicturePath, productPicturePath, notifyUser}}>
            {props.children}
        </ProductContext.Provider>
    )
}

export default ProductState
